@font-face {
  font-family: 'Digital Numbers';
  src: local('Digital Numbers'), url('./fonts/DigitalNumbers-Regular.ttf') format('truetype');
  /* Puedes agregar más formatos de fuentes aquí si los tienes disponibles */
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Rufina';
  src: local('Rufina'), url('./fonts/Rufina-Regular.ttf') format('truetype');
}

body{
  overflow: hidden;
}

.background_fondo {
  background-color: #2E3B81; /* Fondo de color azul */
  color: white; /* Color del texto */
  font-family: 'Rufina';
}
.scoreboard {
  /*border-collapse: collapse;*/
  border-collapse: separate;
  border-spacing: 0;
  width: 95%;
  height: 75%;
  max-width: 100%;
  margin-top: 10px;
  margin-bottom: 10px;
  margin: 10px;
}
.scoreboard th, .scoreboard td {
  border: none; /*1px solid #66A1BA; /* Color de los recuadros de game */
  padding: 10px;
  font-family: 'Digital Numbers', sans-serif; /* Tipo de letra Digital Numbers */
  text-align: center;
}

.text_align_left {
  text-align: left !important; 
}

/* Establece el borde solo en los lados internos de la tabla */
 .scoreboard td:first-child {
  border-left: 1px solid #fcfdfd; /* Ajusta el color y el estilo del borde según tu preferencia */
}

/* Establece el borde solo en los lados internos de la tabla */
.scoreboard th {
  border-bottom: 1px solid #fcfdfd; /* Ajusta el color y el estilo del borde según tu preferencia */
}


/* Establece el borde en la parte inferior de la última fila */
.scoreboard tr:last-child td {
  border-bottom: 1px solid #fcfdfd; 
} /* Elimina el borde inferior de la última fila */
.borde_table {
  border-bottom: 1px solid #fcfdfd; 
}

/* Establece el borde solo en los lados internos de la tabla */
 .scoreboard td:not(:first-child) {
  border-right: 1px solid #66A1BA; /* Ajusta el color y el estilo del borde según tu preferencia */
}
  
/* Estilo para el contenedor principal */
.contenedor {
  width: 100% !important;
  height: 99% !important;
  text-align: center; /* Centrar el contenido horizontalmente */
  margin: 0 auto; /* Centrar el contenedor en la página */
  background-color: #2E3B81;
  color: white;
  overflow-x: auto;
  overflow-y: auto;
  overflow: hidden;
}

/* Estilo para el div de la hora */
.time {
  font-size: 24px; /* Tamaño de la fuente de la hora */
}

/* Estilo para el div con borde */
.bordered-div {
  border: 2px solid black; /* Borde de 2px sólido negro */
  padding: 10px; /* Espacio interno para el contenido */
  margin-top: 20px; /* Espacio superior */
  display: inline-block; /* Para que el div ocupe solo el ancho del contenido */
}

/* Estilos para el div con la hora y borde */
.time-container {  
  border: 2px solid rgb(250, 250, 250); /* Borde de 2px sólido negro */
  padding: 10px; /* Espacio interno para el contenido */
  display: inline-block; /* Para que el div ocupe solo el ancho del contenido */
  font-size: 50px; /* Tamaño de la fuente de la hora */
  font-family: "Digital Numbers", sans-serif !important;
}

.text{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 200px; /* Esto es solo para darle altura al div, puedes ajustarlo según tus necesidades */
  border: 1px;
}

.bottom-text {
  align-self: flex-end;
  border-radius: 20% 20% 0 0;
}
.large_size {
  font-size: 75px;
}

.letra_number{
  font-family: "Digital Numbers", sans-serif !important;
  border-bottom: 1px solid #fcfdfd !important; 
}

.recuadro_games {
  background-color: #66A1BA;
}

.no-border {
  border: none;
}

.circle{
  width: 45px; 
  height: 45px; 
  border-radius: 50%; /* establece la forma de círculo */
  background-color: rgb(248, 248, 243); 
}

.botton-back{
  padding-bottom: 0px !important;
}

.td-titulo {
  border: 1px solid black; /* Borde para visualización */
  padding: 10px; /* Espaciado interno */
  font-family: 'Rufina', sans-serif !important;
}

.bottom-align {
  vertical-align: bottom;
  font-family: 'Rufina', sans-serif !important;
  font-size: 36px ;
}

.borde_nombre {
  border-bottom: 1px solid #fcfdfd !important; 
}
.nombre {
  font-family: 'Rufina', sans-serif !important;
  font-size: 56px;
}
.style_img{
  width: 10em !important;
  display: block; /* Hace que la imagen sea un bloque */
  margin-left: auto; /* Auto para el margen izquierdo */
  margin-right: auto; /* Auto para el margen derecho */
  padding-bottom: 10px;
}

.style_img_left{
  width: 10em !important;
  display: block; /* Hace que la imagen sea un bloque */
  margin-right: auto; /* Auto para el margen derecho */
  padding-bottom: 10px;
}

.custom-modal .modal-dialog {
  max-width: 80%;
}

/* Estilos para pantallas pequeñas */
@media only screen and (max-width: 768px) {
  .contenedor .container {
    width: 100%;
    height: 100vh;
  }
}

/* Estilos para pantallas medianas */
@media only screen and (min-width: 769px) and (max-width: 1024px) {
  .contenedor .container{
    width: 100% !important;
    height: 100vh;
  }
}

/* Estilos para pantallas grandes */
@media only screen and (min-width: 1025px) and (max-width: 1280px) {
  .contenedor .container{
    width: 100% !important;
    height: 150vh;
  }
}

/* Estilos para pantallas extra grandes (televisores) */
@media only screen and (min-width: 1281px) {
  .contenedor .container{
    width: 100% !important;
    height: 150vh;
  }
}
  
